<template lang="pug">
  .container
    br
    article.message.is-info
      .message-header
        p Información de la Suscripción
      .message-body
        .p
          strong Suscriptor:
          | {{ ' ' + tracks.suscriptor }}
        .p
          strong Organización:
          | {{ ' ' + tracks.organizacion }}
        .p
          strong Formato de Fechas:
          | {{ ' ' + tracks.formatoFecha }}
        .p
          strong Fecha de Expiración:
          | {{ ' ' + tracks.fechaFinal }}
        .p
          strong La suscripción caducará en:
          | {{ ' ' + tracks.diasRestantes + ' ' }}
          strong días
      br
</template>
<script>
import trackService from '@/services/track'

export default {
  data: () => ({
    tracks: '',
    isLoading: false
  }),

  methods: {
    getDatosCertificado () {
      this.isLoading = true
      trackService.suscripcion()
        .then(res => {
          // console.log(res)
          this.tracks = res
          this.isLoading = false
        })
    }
  },

  beforeMount () {
    this.getDatosCertificado()
  }
}
</script>

<style lang="scss" scoped>
.title {
  display: flex;
  justify-content: left;
}
</style>



