<template lang="pug">
  .container
    h1.title Parámetros
    article.message.is-info
      .message-header
        p Certificado Digital
      .message-body
        .p
          strong Entidad Dueña del Certificado:
          | {{ ' ' + tracks[0].ownerCert }}
        .p
          strong Entidad Emisora:
          | {{ ' ' + tracks[0].issuerCert }}
        .p
          strong Formato de Fechas:
          | {{ ' ' + tracks[0].formatDate }}
        .p
          strong Fecha de Caducidad:
          | {{ ' ' + tracks[0].dateExpiry }}
        .p
          strong El certificado caducará en:
          | {{ ' ' + tracks[0].daysToExpiry + ' ' }}
          strong días
</template>
<script>
import trackService from '@/services/track'

export default {
  data: () => ({
    tracks: [],
    isLoading: false,
    showNotification: false
  }),

  methods: {
    getDatosCertificado () {
      this.isLoading = true
      trackService.certificado()
        .then(res => {
          // console.log(res)
          console.log(res.length)
          this.showNotification = res.length === 0
          this.tracks = res
          this.isLoading = false
        })
    }
  },

  beforeMount () {
    this.getDatosCertificado()
  }
}
</script>

<style lang="scss" scoped>
.title {
  display: flex;
  justify-content: left;
}
</style>



